@font-face {
  font-family: ff;
  src: url("CONSOLA.212d37c3.TTF");
}

html, body {
  background-color: #4919e0;
  width: 100%;
  height: 100%;
  margin: 0;
}

#main-container {
  color: #fff;
  width: 100%;
  height: 100%;
}

#background-logo {
  z-index: 2;
  transform-origin: center;
  background-image: linear-gradient(#4919e0, #2a113a);
  width: 100%;
  height: 100%;
  font-family: ff;
  position: fixed;
  top: 50%;
  right: 50%;
  overflow: hidden;
  transform: translate(50%, -50%);
}

.background-logo-letters {
  color: #fff;
  opacity: .2;
  margin: 0;
  font-size: 140rem;
  position: relative;
  top: 50%;
  right: 50%;
  transform: translate(35%, -40%)rotate(-10deg);
}

#home-page-container {
  z-index: 3;
  grid-template-rows: 3vh repeat(6, 1fr);
  grid-template-columns: repeat(12, 1fr);
  width: 100%;
  height: 100%;
  font-family: ff;
  display: grid;
  position: relative;
  overflow-x: hidden;
}

#logo {
  grid-area: 2 / 1 / 2 / 6;
  align-self: center;
  gap: 10px;
  margin-left: 5vw;
  font-size: 1vw;
  display: flex;
}

#company-name {
  color: #fff;
  align-self: center;
  margin-top: 0;
  margin-right: 10px;
}

#logo2 {
  z-index: 4;
  justify-content: space-evenly;
  width: 25vw;
  font-size: 1vw;
  display: flex;
  position: sticky;
  top: 20px;
}

#description {
  grid-area: 2 / 9 / 2 / 12;
  font-size: 1.4vw;
}

#description-title {
  justify-content: space-between;
  width: 100%;
  display: flex;
}

#company-description {
  word-spacing: 5px;
}

#catchphrase {
  text-align: center;
  grid-area: 7 / 2 / 7 / 12;
  font-size: 6vw;
}

#catchphrase span {
  border: 3px solid #fff;
  width: 4vw;
  display: inline-block;
  translate: 0 -1vw;
}

#catchphrase br {
  display: none;
}

.logo-divider {
  border: 2px solid #fff;
  height: 5vw;
  margin: 0;
  rotate: 8deg;
}

.logo-dots {
  color: #fff;
  font-size: 4vw;
  font-weight: bold;
}

#directory {
  z-index: 3;
  background-color: #0000;
  font-family: ff;
  position: relative;
}

#directory-container {
  color: #fff;
  background-image: linear-gradient(to top, #4919e0, #2a113a);
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-size: 4vw;
  display: flex;
}

#container-list {
  text-align: center;
  flex-direction: column;
  align-items: end;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.list {
  margin-top: 25vw;
}

.list-item {
  border-top: 2px solid #fff;
  width: 65%;
  margin: 0 70px 0 0;
}

.list-item:nth-child(5) {
  border-bottom: 2px solid #fff;
}

.list-digits {
  margin-right: 100%;
  font-size: 1.5vw;
}

.list-item p {
  text-align: left;
  word-spacing: 25px;
  margin: 0;
  font-size: 6vw;
}

.list-link {
  color: #fff;
  margin-left: 29vw;
  font-size: 1.5vw;
  text-decoration: none;
}

#footer {
  text-align: center;
  border: 2px solid #fff;
  border-radius: 20px;
  width: 70%;
  margin: 150px auto;
  font-size: 7vw;
}

#footer a {
  color: #fff;
  padding: 40px 0;
  text-decoration: none;
  display: inline-block;
}

@media screen and (width <= 480px) {
  #main-container {
    color: #fff;
    width: 100%;
    height: 100%;
  }

  #home-page-container {
    z-index: 3;
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: 100%;
    width: 100%;
    height: 100%;
    display: grid;
    position: relative;
  }

  #logo {
    grid-area: 1 / 1 / 1 / 1;
    justify-content: center;
    margin-left: 0;
    font-size: 3vw;
    display: flex;
  }

  #directory {
    overflow-x: hidden;
    height: 170% !important;
  }

  #logo2 {
    display: none;
  }

  #description {
    grid-area: 3 / 1 / 3 / 1;
    align-self: center;
    margin: 0 7px;
    font-size: 6vw;
  }

  .logo-divider {
    height: revert;
    border: 1px solid #fff;
    margin: 0;
    rotate: 8deg;
  }

  .logo-dots {
    color: #fff;
    font-size: 14vw;
    font-weight: bold;
  }

  #catchphrase {
    text-align: center;
    grid-area: 2 / 1 / 2 / 1;
    font-size: 13vw;
  }

  #catchphrase span {
    border: 6px solid #fff;
    width: 14vw;
    display: inline-block;
    translate: 0 -1vw;
  }

  #catchphrase br {
    display: inline;
  }

  #directory {
    width: 100%;
    height: 150%;
  }

  .list-item {
    border-top: 2px solid #fff;
    border-bottom: 2px solid #fff;
    width: 90%;
    margin: auto;
    padding: 15px 0;
  }

  .list-digits {
    margin-right: 100%;
    font-size: 5.5vw;
  }

  .list-item p {
    word-spacing: revert;
    margin: 0;
    font-size: 10vw;
  }

  .list-link {
    margin-left: 35vw;
    font-size: 5.5vw;
  }

  #footer {
    text-align: center;
    border: 2px solid #fff;
    border-radius: 10px;
    width: 90%;
    margin: 90px auto;
    font-size: 10vw;
  }
}

